@import '';

body,
html,
.content,
.toss-container {
  height: 100%;
  width: 100%;
}

.toss-container {
  text-align: center;
  background: #eeeeee;
}

.spin {
  -webkit-animation: spin infinite 3s ease-out forwards;
          animation: spin infinite 3s ease-out forwards;
  height: 80px;
}

.toss-header {
  background-color: #222;
  -webkit-box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
          box-shadow: 0px 2px 4px -1px rgba(0,0,0,0.2), 0px 4px 5px 0px rgba(0,0,0,0.14), 0px 1px 10px 0px rgba(0,0,0,0.12);
  height: 30px;
  padding: 20px;
  color: white;
}

.toss-header h1 {
  margin: 0px;
}

.toss-result {
  color: #fff;
  font-size: 1.5em;
  height: 1.5em;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.tosser button {
  height: 150px;
  width: 150px;
  border: 0px;
  border-radius: 50%;
  outline: none;
  -webkit-box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 6px 10px 0px rgba(0,0,0,0.14), 0px 1px 18px 0px rgba(0,0,0,0.12);
          box-shadow: 0px 3px 5px -1px rgba(0,0,0,0.2), 0px 6px 10px 0px rgba(0,0,0,0.14), 0px 1px 18px 0px rgba(0,0,0,0.12);
  background: #f7d055;
}

.progressbar {
  color: green;
}

.toss-trigger { 
  position: relative;
  outline: none;
  margin-top: 100px;
}


@-webkit-keyframes spin {
    from { -webkit-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(1800deg); transform: rotateY(1800deg); }
}


@keyframes spin {
    from { -webkit-transform: rotateY(0); transform: rotateY(0); }
    to { -webkit-transform: rotateY(1800deg); transform: rotateY(1800deg); }
}


